@import "../../scss//variables.scss";

.container {
  margin: 25px;
  padding: 25px 50px;
  background-color: rgba(206, 188, 143, 0.4);
  border-radius: 25px;
  color: $shell-mid-brown;
  border: solid 1px $shell-sand;

  .children {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
  }
}

@media only screen and (max-width: 780px) {
  h2 {
    font-size: 1.2em
  }
}